<template>
	<div v-if="!getDetail.loading">
		<mcontent></mcontent>
		<mcomment></mcomment>
	</div>
	
</template>
<script>
import {getDetailTopic,initTopic} from '../vuex/action'
	export default {
		data(){
			return{
				author:''
			}
		},
		vuex:{
			actions:{
				getDetailTopic,
				// initTopic
			},
			getters:{
				getDetail:({showTopic})=>showTopic.data
			}
		},
		route:{
			data(transition){
				// console.log(transition.to.path)
				this.getDetailTopic(transition.to.path);
				console.log(333)
				transition.next()
			}
		},
		components:{
			mcontent:require('../components/content.vue'),
			mcomment:require('../components/comment.vue')
		}
		// ready(){
		// 	this.author=this.getDetail.author;
		// }
	}
</script>
<style scoped>
</style>


